.tiktok-feed-feed {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
	font-size  : 16px;
	line-height: 1;

	.tiktok-feed-item,
	.tiktok-feed-video,
	.tiktok-feed__actions {
		opacity   : 0;
		transition: all 0.4s;
	}

	&.loaded {

		.tiktok-feed-item,
		.tiktok-feed-video,
		.tiktok-feed__actions {
			opacity: 1;
		}
	}

	a {
		text-decoration: none !important;
	}
}