.tiktok-feed-item {
  &__video-card {
    background-color: white;
    font-size: 12px;
    line-height: 1.2;
    font-weight: 600;
  }

  &__info,
  &__video_description {
    display: flex;
    clear: both;
    margin: 0;
    padding: 1em 0;
  }

  &__video_description {
    display: block;
  }
  &__icons {
    margin: 0 0 0 auto;
  }

  // .ig-card-link,
  &__comments_count,
  &__likes_count {
    i {
      font-weight: bold;
      margin: 0 2px;
    }
  }

  &__info {
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    font-size: 0.9em;
    margin: 0 2px;
  }

  &__date {
    display: inline-block;
    text-transform: uppercase;
    opacity: 0.6;
  }

  &__comments_count,
  &__likes_count {
    display: inline-block;
    margin: 0;
  }

  &__date {
    display: inline-block;
    margin: 0 5px 0px 0;
  }
}
