.tiktok-feed-modal {
  z-index: 3000;
  max-width: 600px;
  margin: auto;
  background: none;
  text-align: right;
  transition: all 0.3s linear;
  animation-name: Modal-animation;
  animation-duration: 0.3s;
  animation-timing-function: ease-in-out;

  &__options {
    display: flex;
    justify-content: space-between;

    &--arrows {
      display: flex;
      flex-direction: row;
      width: 100px;
      justify-content: space-between;
    }
  }

  &__body {
    background-color: #ffff;
    width: 100%;
    max-height: 800px;
    overflow: auto;
    margin: auto;
    text-align: left;
    display: flex;
    border-radius: 8px;
  }
}

@media (max-width: 48em) {
  .tiktok-feed-modal {
    max-width: 60vw;

    &__body {
      flex-direction: column;
      overflow-x: hidden;
      max-height: 80vh;
    }
  }

  /* width */
  ::-webkit-scrollbar {
    width: 3px;
    height: 3px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background-color: rgba(#0b0b0b, 0.4);
    border-radius: 10px;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(1, 106, 167);
    border-radius: 10px;
  }
}

@media (max-width: 27em) {
  .tiktok-feed-modal {
    max-width: 90vw;
  }
}

@keyframes Modal-animation {
  from {
    top: -100%;
  }

  to {
    top: 50%;
  }
}
