$primary-color: #fe2c55;
$dark-color   : #262626;
$gray-color   : #eeeeee;
$light-color  : #c32a67;
$text-color   : #999999;

@-moz-keyframes spin {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}

@-webkit-keyframes spin {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}