.tiktok-feed-item {
  &__video-wrap {
    display: block;
    position: relative;
    text-align: center;
    background-color: $gray-color;
    cursor: pointer;
  }

  &__video {
    width: 100%;
    height: auto;
    overflow: hidden;
    vertical-align: middle;
  }

  &__icon,
  &__video {
    transition: all 0.3s;
  }

  &__icon {
    position: absolute;
    z-index: 1;
    font-size: 16px;
    color: white;
    opacity: 0.66;
    top: 0;
    margin: 10px;
    right: 0;
    text-shadow: 1px 1px rgba(0, 0, 0, 0.15);
    display: flex;
    text-decoration: none;

    &--views {
      top: 50%;
      left: 50%;
      margin: -12px auto auto -8px;
      font-size: 24px;
      width: 24px;
      height: 24px;
    }

    &--views_count {
      top: auto;
      bottom: 0;
      opacity: 0;
      right: auto;
      left: 0;
      pointer-events: none;
    }

    > span {
      margin: 0 5px;
      line-height: 16px;
      font-size: 13px;
      font-weight: 600;
      position: relative;
      top: -1px;
    }
  }
}
