.tiktok-feed-modal {
  &__video-container {
    width: 70%;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    &--iframe {
      margin: 15px 10px;
    }
    &--video {
      margin: 15px;
    }
  }

  &__spinner {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;

    &--img {
      max-height: 70vh;
      max-width: 100%;
      object-fit: cover;
      filter: blur(8px);
    }

    .tiktok-feed__spinner {
      position: absolute;
    }
  }

  &__video {
    max-height: 600px;
    max-width: 100%;
    object-fit: contain;
    min-height: 100%;
    border-radius: 10px;
  }

  &__iframe-container {
    overflow: hidden;
    border-radius: 10px;
    blockquote {
      margin: auto !important;
      padding: 0 !important;
      border: none !important;
      box-shadow: none !important;
      position: relative !important;
    }
  }

  @media (max-width: 48em) {
    &__video-container {
      width: 100%;
      overflow: visible;
      &--iframe,
      &--video {
        margin: 0;
      }
    }

    &__video,
    &__iframe-container {
      border-radius: 0;
    }
  }

  @media (max-width: 27em) {
    &__iframe-container {
      margin: 0;
    }
  }
}
