.tiktok-feed-item {
  box-sizing: border-box;
  overflow: hidden;
  display: block;
  line-height: 0;

  &--cols-1 {
    width: 100%;
  }

  &--cols-2 {
    width: 50%;

    &.highlight {
      width: 100%;
    }
  }

  &--cols-3 {
    width: 33.333%;

    &.highlight {
      width: 66.666%;
    }
  }

  &--cols-4 {
    width: 25%;

    &.highlight {
      width: 50%;
    }
  }

  &--cols-5 {
    width: 20%;

    &.highlight {
      width: 40%;
    }
  }

  &--cols-6 {
    width: 16.666%;

    &.highlight {
      width: 33.333%;
    }
  }

  &--cols-7 {
    width: 14.285%;

    &.highlight {
      width: 42.855%;
    }
  }

  &--cols-8 {
    width: 12.5%;

    &.highlight {
      width: 37.5;
    }
  }

  &--cols-9 {
    width: 11.111%;

    &.highlight {
      width: 33.333%;
    }
  }

  &--cols-10 {
    width: 10%;

    &.highlight {
      width: 30%;
    }
  }

  &--cols-11 {
    width: 9.09%;

    &.highlight {
      width: 27.27%;
    }
  }

  &--cols-12 {
    width: 8.33%;

    &.highlight {
      width: 33.32%;
    }
  }

  &--cols-13 {
    width: 7.692%;

    &.highlight {
      width: 30.768%;
    }
  }

  &--cols-14 {
    width: 7.142%;

    &.highlight {
      width: 28.568%;
    }
  }

  &--cols-15 {
    width: 6.666%;

    &.highlight {
      width: 26.664%;
    }
  }

  &--cols-16 {
    width: 6.25%;

    &.highlight {
      width: 31.25%;
    }
  }

  &--cols-17 {
    width: 5.882%;

    &.highlight {
      width: 29.41%;
    }
  }

  &--cols-18 {
    width: 5.555%;
  }

  &--cols-19 {
    width: 5.263%;

    &.highlight {
      width: 33.33%;
    }
  }

  &--cols-20 {
    width: 5%;

    &.highlight {
      width: 30%;
    }
  }

  &__wrap {
    width: 100%;
    //background-color: $gray-color;
    overflow: hidden;
  }

  &:hover {
    .tiktok-feed-item__icon {
      &--views_count {
        opacity: 0.8;
        pointer-events: all;
        z-index: 99;
        &:hover {
          opacity: 1;
        }
      }

      // &.tiktok-feed-video-play,
      // &.qlttf-icon-gallery {
      // 	opacity       : 0;
      // 	pointer-events: none;
      // }
    }

    .tiktok-feed-item__video-mask {
      opacity: 1;
      transition: all 0.5s ease-out;
      &--content {
        opacity: 1;
      }
    }
  }
}

// Square
// ---------------------------------------------------------------------------
.tiktok-feed-square {
  .tiktok-feed-item {
    display: flex;
    justify-content: center;
    align-items: stretch;
    box-sizing: border-box;

    &__video-wrap {
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      width: 100%;
      padding-top: 100%;
      // .tiktok-feed-link {
      // 	position: static;
      // }
    }

    &__video {
      overflow: hidden;
      object-fit: cover;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
    }
  }
}
