// Alert
// -----------------------------------------------------------------------------

.tiktok-feed__alert {
	background-color: $primary-color;
	padding         : 15px;
	color           : white;
	border-radius   : 3px;
	margin          : 0 0 15px 0;
	width           : 100%;
	display         : block;

	&::after,
	&::before {
		content: " ";
		display: table;
	}

	&::after {
		clear: both;
	}

	.button {
		float: right;
	}

	ul {
		padding: 0;
		margin : 0;
	}

	li {
		list-style  : none outside !important;
		padding-left: 0 !important;
		margin-left : 0 !important;
	}
}