// Profile
// ---------------------------------------------------------------------------

.tiktok-feed__profile {
  width: auto;
  margin: 15px 0;

  &,
  .avatar,
  .info {
    height: 46px;
    display: flex;
    align-items: center;
  }

  img {
    border: none;
    width: 46px;
    height: 46px;
    margin-right: 10px;
    border-radius: 60px;
  }

  .avatar {
    margin: 0 10px 0 0;
  }

  .separator,
  .follow,
  .user {
    margin: 0;
    padding: 0;
    text-decoration: none;
    font-weight: 600;
    font-size: 16px;
    color: $dark-color;
    display: inline-block;
    vertical-align: middle;
    line-height: 18px;
  }

  .separator {
    color: $dark-color;
    margin-left: 4px;
    margin-right: 4px;
  }

  .follow {
    color: $primary-color;

    &:hover {
      color: $dark-color;
    }
  }

  .biography {
    line-height: 18px;
    font-size: 12px;
    font-weight: 600;
  }
}
