.tiktok-feed-modal {
	&__video-info {
		width      : 300px;
		font-size  : 13px;
		font-weight: 400;
		color      : #262626;
		padding    : 0;
	}

	&__profile {
		display      : flex;
		align-items  : center;
		border-bottom: 1px solid #eeeeee;
		width        : auto;
		font-weight  : bold;
		padding      : 20px;

		img {
			border       : none;
			width        : 48px;
			height       : 48px;
			margin-right : 20px;
			border-radius: 40px;
		}

		span {
			display       : block;
			text-transform: uppercase;
			color         : #262626;
			font-weight   : 700;
		}

		a {
			margin         : 0;
			padding        : 0;
			text-decoration: none;
			font-size      : 13px;
			color          : #999999;
		}
	}

	&__icons {
		padding: 20px;
	}

	&__counter,
	&__link {
		position   : inherit;
		display    : inline-block;
		font-size  : 12px;
		font-weight: 400;
		color      : #999999;
		margin     : 0 20px 0 0;
		line-height: 1;

		svg {
			vertical-align: text-bottom;
		}
	}

	&__video_description {
		padding       : 20px;
		padding-top   : 0;
		padding-bottom: 0;
		margin-bottom : 20px;
		overflow      : auto;

		a {
			text-decoration: none;
			color          : $primary-color;
			font-weight    : 600;
		}
	}

	&__info {
		border-top: 1px solid #eeeeee;
		padding   : 20px;
	}

	&__likes_count,
	&__comments_count,
	&__date {
		display: inline-block;
		margin : 0 5px 0px 0;

		i {
			margin     : 0 4px 0px 0;
			font-weight: bold;
		}
	}

	&__date {
		text-transform: uppercase;
		font-size     : 12px;
		color         : #999999;
		float         : right;
		position      : relative;
		bottom        : -1px;
	}

	@media (max-width: 48em) {
		&__video-info {
			width: 100%;
		}

		&__video_description {
			width: auto;
		}
	}
}