@font-face {
  font-family: "qlttf";
  src: url(data:font/opentype;charset=utf-8;base64,);
  src: url(data:font/opentype;charset=utf-8;base64,)
      format("embedded-opentype"),
    url(data:font/opentype;charset=utf-8;base64,)
      format("truetype"),
    url(data:font/opentype;charset=utf-8;base64,)
      format("woff"),
    url(data:font/opentype;charset=utf-8;base64,)
      format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="qlttf-icon-"],
[class*=" qlttf-icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "qlttf" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.qlttf-icon-gallery:before {
  content: "\e90d";
}
.qlttf-icon-clock-o:before {
  content: "\e90c";
}
.qlttf-icon-upload:before {
  content: "\e900";
}
.qlttf-icon-share-o:before {
  content: "\e902";
}
.qlttf-icon-comment:before {
  content: "\e903";
}
.qlttf-icon-heart:before {
  content: "\e904";
}
.qlttf-icon-prev:before {
  content: "\e906";
}
.qlttf-icon-prev-o:before {
  content: "\e907";
}
.qlttf-icon-next:before {
  content: "\e908";
}
.qlttf-icon-next-o:before {
  content: "\e909";
}
.qlttf-icon-heart-o:before {
  content: "\e90a";
}
.qlttf-icon-comment-o:before {
  content: "\e90b";
}

.qlttf-icon-download:before {
  content: "\e900";
  display: inline-block;
  transform: rotate(180deg);
}