.tiktok-feed-item {
  &__video-mask {
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 8;
    transition: all 0.5s ease-out;
    opacity: 0;
    background: rgba(0, 122, 255, 0.55);
    width: 100%;
    height: 100%;

    &--content {
      position: absolute;
      top: 50%;
      left: 0px;
      right: 0;
      margin: -10px 0 0 0;
      z-index: 9;
      width: 100%;
      color: white;
      transition: all 0.5s ease;
      opacity: 0;
      line-height: 20px;
      font-size: 18px;
      text-align: center;
    }
  }

  &__likes_count--mask,
  &__comments_count--mask {
    padding: 0 4px;
    font-size: 15px;
    font-weight: 600;
    vertical-align: middle;
    line-height: 20px;
  }

  &__icon-heart,
  &__icon-bubble_chat {
    font-size: 12px;
    margin-right: 3px;
    font-weight: 800;
    color: $primary-color;
  }
}
