.tiktok-feed-modal__button-arrow {
  background: none;
  border: none;
  width: auto;
  display: flex;
  align-items: center;
  border-radius: 100%;
  margin: 0;
  font-size: 1.5rem;
  color: #fff;
  opacity: 0.6;
  text-align: center;
  transition: all 0.2s;
  cursor: pointer;

  &:hover {
    opacity: 1;
    // background-color: rgba(#ccc, .3)
  }
}

@media (min-width: 27.0625rem) {
  .tiktok-feed-modal__button-arrow {
    position: absolute;
    padding: 10px;
    font-size: 2rem;
    top: 50vh;

    &:hover {
      background-color: rgba(#ccc, 0.3);
    }
  }

  .qlttf-icon-prev {
    left: 10px;
  }

  .qlttf-icon-next {
    right: 10px;
  }
}
