.tiktok-feed-list {

	// Responsive
	// -------------------------------------------------------------------------

	&[data-feed_layout="masonry"],
	&[data-feed_layout="gallery"] {
		.tiktok-feed-item {
			@media screen and (max-width: 1023px) and (min-width: 768px) {
				min-width: 33.333%;
			}

			@media screen and (max-width: 767px) {
				min-width: 33.333%;
			}

			@media screen and (max-width: 480px) {
				min-width: 50%;
			}
		}
	}

	// Masonry
	// -------------------------------------------------------------------------

	&[data-feed_layout="masonry"] {
		.tiktok-feed-item {
			position: absolute;
		}
	}
}