.tiktok-feed-list {
  // Carousel
  // ---------------------------------------------------------------------------

  &[data-feed_layout="carousel"] {
    // overflow: hidden;
    display: flex;

    .swiper-button-next,
    .swiper-button-prev {
      transition: all 0.3s;
      background: none;
      top: 0;
      bottom: 0;
      height: 100%;
      margin: 0;
      background: transparent;
      width: 32px;
      display: flex;
      align-items: center;

      > i {
        text-align: center;
        color: $primary-color;
        width: 32px;
        font-size: 26px;
      }

      &:hover {
        background-color: rgba(0, 0, 0, 0.2);
      }
    }

    .swiper-button-prev {
      left: 0;
    }

    .swiper-button-next {
      right: 0;
    }

    .swiper-pagination-bullet-active {
      background-color: $primary-color;
    }
  }
}
